ected {\n      color: $primary-color;\n      stroke: $primary-color;\n\n      .edge-text {\n        fill: $light-color;\n        stroke: $light-color;\n      }\n    }\n  }\n\n  .edge-mouse-handler {\n    stroke: black;\n    opacity: 0;\n    color: transparent;\n    stroke-width: 15px;\n    cursor: pointer;\n    pointer-events: all;\n  }\n\n  .arrow {\n    fill: $primary-color;\n  }\n\n  .graph-controls {\n    position: absolute;\n    bottom: 30px;\n    left: 15px;\n    display: grid;\n    grid-template-columns: auto auto auto;\n    grid-gap: 15px;\n    align-items: center;\n    user-select: none;\n\n    > .slider-wrapper {\n      background-color: $light-color;\n      color: $primary-color;\n      border: solid 1px $light-grey;\n      padding: 6.5px;\n      border-radius: 2px;\n\n      > span {\n        display: inline-block;\n        vertical-align: middle;\n      }\n\n      > .slider {\n        position: relative;\n        margin-left: 4px;\n        margin-right: 4px;\n        cursor: pointer;\n        vertical-align: middle;\n      }\n    }\n\n    .slider-button {\n      background-color: $light-color;\n      fill: $primary-color;\n      border: solid 1px $light-grey;\n      outline: none;\n      width: $button-size;\n      height: $button-size;\n      border-radius: 2px;\n      cursor: pointer;\n      margin: 0;\n\n      > svg {\n        vertical-align: middle;\n      }\n    }\n  }\n\n  .circle {\n    fill: $light-grey;\n  }\n}\n\n"],"sourceRoot":""}]);
